  font-family: "Roboto", sans-serif;
  font-weight: 300;
  line-height: 25px;
}
.section-faq__ask-more-btn {
  padding-left: 4px;
  color: black;
  text-decoration: underline;
}
.section-faq__questions-list {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  list-style-type: none;
}
.section-faq__question {
  font-size: 36px;
  font-size: 17px;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  line-height: 25px;
}

.section {
  scroll-margin-top: 100px;
}

@media screen and (min-width: 1000px) {
  .section {
    scroll-margin-top: 200px;
  }
}
.main {
  width: 100vw;
  height: 100%;
  background: #fff;
}

.page_wrapper {
  min-height: 100vh;
  background-color: #f7f7f7;
  overflow-x: hidden;
}

.scrollToSectionLink {
  text-decoration: none;
}

@media screen and (max-width: 1000px) {
  .upper-header,
  .bottom-header {
    display: none;
  }
  .header {
    height: 100px;
  }
  .main {
    margin-top: 100px;
  }
}
